import axios, { AxiosInstance } from 'axios';
import { util } from '@/utils/utils'

declare module '@vue/runtime-core' {
  interface ComponentCustomProperties {
    $api: Api;
  }


}


class Api {
  private env = process.env.NODE_ENV;
  //Add https ip address 
  private api: AxiosInstance = axios.create({ baseURL:  this.env === 'development' ? 'https://localhost:5500/api' : 'https://200.88.116.162:5500/api'}); 

  async get(route: string, reqQuery={}){
      try {

      const rows = await this.api.get(route, { params: reqQuery });
    if(rows.data.data.nodb){
        util.notify({message: 'No se pudo establecer conexión con la base de datos'});
        return {success: true, data: []};
      }

      if(rows.data.success === false){
          util.notify({message: 'Error del servidor'});
        }
      return rows.data;



    } catch (error) {
        util.notify({message: 'Error del servidor'});
        return {success: false, data: []};

    }
  }
  async post(route: string, body: object){
      if(!body) return 'No data to post';

    try {

      const rows = await this.api.post(route, body);

      if(rows.data.data.nodb){
        util.notify({message: 'No se pudo establecer conexión con la base de datos'});
      }

      return rows.data;

    } catch (error) {

        util.notify({message: 'Error del servidor'});
        return {success: false, data: []};

    }
  }

  async put(route: string, body: object){
      if(!body) return 'No data to put';

    try {

      const rows = await this.api.put(route, body);

      if(rows.data.data.nodb){
        util.notify({message: 'No se pudo establecer conexión con la base de datos'});
      }

      return rows.data;

    } catch (error) {

        util.notify({message: 'Error del servidor'});
        return {success: false, data: []};

    }
  }
  async delete(route: string, reqParams={}){
    try {

      const rows = await this.api.delete(route,  { params: reqParams });
      return rows.data;

    } catch (error) {

        return {success: false, data: []};

    }
  } 
}


const api = new Api();

export default api;
